<template>
  <!--
    根目录规范(必须不能为空)：
    idm-ctrl：控件类型 drag_container：容器，drag_container_inlieblock：行内容器，idm_module：非容器的组件
    id：使用moduleObject.id，如果id不使用这个将会被idm-ctrl-id属性替换
    idm-ctrl-id：组件的id，这个必须不能为空
  -->
  <div idm-ctrl="idm_module" :id="moduleObject.id" :idm-ctrl-id="moduleObject.id" style="position: relative;">
    <!--
      组件内部容器
      增加class="drag_container" 必选
      idm-container-index  组件的内部容器索引，不重复唯一且不变，必选
    -->
    <!-- <img :src="IDM.url.getModuleAssetsWebPath(require('../assets/ExternalLinksComponents.png'),moduleObject)" /> -->
    <!-- {{$root.propData}} -->
    <iframe :src="propData.url" style="width:100%;height:400px;border:none"/>
    <div class="iframe-dev-shade" v-if="moduleObject.env=='develop'"></div>
  </div>
</template>

<script>
export default {
  name: 'IImage',
  data(){
    return {
      moduleObject:{},
      propData:this.$root.propData.compositeAttr||{}
    }
  },
  props: {
  },
  created() {
    this.moduleObject = this.$root.moduleObject
  },
  mounted() {
  },
  destroyed() {},
  methods:{
    /**
     * 提供父级组件调用的刷新prop数据组件
     */
    propDataWatchHandle(propData){
      this.propData = propData.compositeAttr||{};
    }
  }
}
</script>
<style scoped>
.iframe-dev-shade{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 2;
}
</style>